import React from "react";
import ReactTable from "react-table-6";
import "react-table-6/react-table.css";

const Data = () => {
  const columns = [
    {
      Header: "ID",
      accessor: "id"
    },
    {
      Header: "Name",
      accessor: "name"
    },
    {
      Header: "Visited Places",
      accessor: "visited"
    }
  ];
  const users = [
    {
      id: "1",
      name: "Raj Shah",
      visited: "Mumbai"
    },
    {
      id: "2",
      name: "Rishi Shah",
      visited: "Goa"
    },
    {
      id: "3",
      name: "Darsh Shah",
      visited: "USA"
    },
    {
      id: "4",
      name: "Vipul Shah",
      visited: "South Africa"
    },
    {
      id: "5",
      name: "Heena Shah",
      visited: "Dubai"
    },
    {
      id: "6",
      name: "Aditya Kuar",
      visited: "South Korea"
    },
    {
      id: "7",
      name: "Dhruv Desai",
      visited: "London"
    }
  ];
  return (
    <ReactTable
      data={users}
      columns={columns}
      defaultPageSize={2}
      pageSizeOptions={[2, 4, 6]}
    />
  );
};

export default Data;
